
export const signInWithEmailAndPassword = (credentials) => {
    return (dispatch, getState, { getFirebase }) => {

        const firebase = getFirebase()

        firebase.auth().signInWithEmailAndPassword(
            credentials.email,
            credentials.password
        ).then(() => {
            dispatch({ type: 'LOGIN_SUCCESS' })
        }).catch((err) => {
            dispatch({ type: "LOGIN_ERROR", err })
        })
    }
}

export const signUpWithEmailAndPassword = (newUser) => {
    return (dispatch, getState, { getFirebase, getFirestore }) => {
        const firebase = getFirebase()
        const firestore = getFirestore()

        firebase.auth().createUserWithEmailAndPassword(
            newUser.email,
            newUser.password
        ).then((resp) => {
            return firestore.collection('users').doc(resp.user.uid).set({
                name: newUser.name,
            })
        }).then(() => {
            dispatch({ type: "SIGNUP_SUCCESS" })
        }).catch((err) => {
            dispatch({ type: "SIGNUP_ERROR", err })
        })
    }
}

export const signInWithGoogle = () => {
    return (dispatch, getState, { getFirebase, getFirestore }) => {
        const firebase = getFirebase()
        const firestore = getFirestore()

        var provider = new firebase.auth.GoogleAuthProvider();

        firebase.auth().signInWithPopup(provider).then((result) => {
            console.log("hit")

            var docRef = firestore.collection("users").doc(result.user.uid);

            return docRef.get().then(function (doc) {
                if (!doc.exists) {
                    return firestore.collection('users').doc(result.user.uid).set({
                        name: result.user.displayName,
                    })
                }
            })
        }).then(() => {
            console.log("google login success")
            dispatch({ type: "LOGIN_SUCCESS" })
        }).catch((err) => {
            console.log(err)
            dispatch({ type: "LOGIN_ERROR", err })
        })
    }
}

export const signInWithFacebook = () => {
    return (dispatch, getState, { getFirebase, getFirestore }) => {
        const firebase = getFirebase()
        const firestore = getFirestore()
        var provider = new firebase.auth.FacebookAuthProvider();

        firebase.auth().signInWithPopup(provider).then((result) => {

            var docRef = firestore.db.collection("users").doc(result.user.uid);

            return docRef.get().then(function (doc) {
                if (!doc.exists) {
                    return firestore.collection('users').doc(result.user.uid).set({
                        name: result.user.displayName,
                    })
                }
            })
        }).then(() => {
            dispatch({ type: "LOGIN_SUCCESS" })
        }).catch((err) => {
            dispatch({ type: "LOGIN_ERROR", err })
        })
    }
}