import posed, { PoseGroup } from 'react-pose';
import React, { Component } from 'react'
import { item } from './ItineraryAnim.module.scss'
import { Timeline } from 'antd';

const Item = posed.li({});

export default class ItineraryAnim extends Component {
  state = {
    items: [0, 1, 2, 3],
    itemDesc: ["a", "b", "c", "d", "e"]

  };

  componentDidMount() {
    this.interval = setInterval(this.toggleVisibility, 3000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  toggleVisibility = () => this.setState({ items: shuffle(this.state.items) });

  render() {
    return (
      <Timeline>
        <Timeline.Item key={6}><div className={item}></div>
        </Timeline.Item>
        <PoseGroup>
          {this.state.items.map(i => (
            <Item data-key={i} key={i}>
              <Timeline.Item>
                <div className={item} data-key={i}></div>
              </Timeline.Item>
            </Item>
          ))}
          <Timeline.Item key={6}><div className={item} />
          </Timeline.Item>
        </PoseGroup>

      </Timeline>
    );
  }
}

function shuffle(array) {
  var currentIndex = array.length,
    temporaryValue,
    randomIndex;

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {
    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // And swap it with the current element.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }

  return array;
}