import { Tag } from 'antd';
import React from 'react';
const { CheckableTag } = Tag;

export default class CheckAnim extends React.Component {
    state = { checked: true };

    toggleVisibility = () => this.setState({ checked: Math.random() >= 0.5 });

    componentDidMount() {
        this.interval = setInterval(this.toggleVisibility, 3000);
    }

    componentWillUnmount() {
        clearInterval(this.interval);
    }

    render() {
        return <CheckableTag {...this.props} checked={this.state.checked} />;
    }
}